import React from 'react';
// import {Button} from 'antd';
import {Button} from 'primereact/button';
class Box4 extends React.Component {

  render() {
    return (
      <div className="box" style={{width:"100%"}} align="center">
        <Button label="Ghi&nbsp;giao&nbsp;dịch" style={{width:110, height:45, marginLeft:20, fontFamily:"Tahoma"}} />
        <Button label="Xem&nbsp;hóa&nbsp;đơn" style={{width:110, height:45, marginLeft:20, fontFamily:"Tahoma"}} />
        <Button label="Lưu&nbsp;hóa&nbsp;đơn" style={{width:110, height:45, marginLeft:20, fontFamily:"Tahoma"}} />
        <Button label="Nhập&nbsp;lại" style={{width:110, height:45, marginLeft:20, fontFamily:"Tahoma"}} />
        <Button type="reset" label="Thoát" style={{width:110, height:45, marginLeft:20, fontFamily:"Tahoma"}} />
      </div>
    );
  }
}
export default Box4;
