import React from 'react';
import MyCalendar from './Calendar';
// import {Button} from 'antd';
import {Button} from 'primereact/button';
import MyDropdown from './Dropdown';

var data = require('./data.json');
class Box1 extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      ngayBan: null,
      httt: null,
      nv1: null,
      nv2: null,
      lyDo:null,
      loaiGD: null,
      bh:null
    }
  }
  dateChange = value => {
    console.log(value+"_dateChange");
    this.setState({ngayBan: value});
  }
  drdHTTTChange = item => {
    console.log(item);
    this.setState({httt: item});
  }
  drdNV1Change = item => {
    console.log(item);
    this.setState({nv1: item});
  }
  drdNV2Change = item => {
    console.log(item);
    this.setState({nv2: item});
  }
  drdLyDoChange = item => {
    console.log(item);
    this.setState({lyDo: item});
  }
  drdLoaiGDChange = item => {
    console.log(item);
    this.setState({loaiGD: item});
  }
  drdBHChange = item => {
    console.log(item);
    this.setState({bh: item});
  }
  btnBaoLanhClick = () => {
    if(this.state.ngayBan)
      if(this.state.httt)
        if(this.state.nv1)
          if(this.state.nv2)
            if(this.state.lyDo)
              if(this.state.loaiGD)
                if(this.state.bh)
                   return alert(JSON.stringify(this.state, null, 4));
    alert("Vui lòng nhập đầy đủ thông tin");
  }
  render() {
    return (
      <div className="box">
        <div className="column" style={{width:"50%"}}>
          <div className="item">
            <div className="label">Ngày bán</div>
            <div className="input p-fluid">
              <MyCalendar onChange={this.dateChange} />
            </div>
          </div>
          <div className="item">
            <div className="label">HTTT</div>
            <div className="input">
              <MyDropdown onChange={this.drdHTTTChange} list={data.ListHTTT} placeholder="Chọn Hình thức thanh toán."/>
            </div>
          </div>
          <div className="item">
            <div className="label">NV bán hàng</div>
            <div className="input">
              <MyDropdown onChange={this.drdNV1Change} width="65%" list={data.ListNVBH} placeholder="Chọn Nhân viên bán hàng."/>
              <MyDropdown onChange={this.drdNV2Change} width="35%" list={data.ListNVBH} disabled={!this.state.nv1}/>
            </div>
          </div>
        </div>
        <div className="column" style={{width:"50%"}}>
          <div className="item">
            <div className="label">Lý do</div>
            <div className="input">
              <MyDropdown onChange={this.drdLyDoChange} list={data.ListLyDo} placeholder="Chọn lý do."/>
            </div>
          </div>
          <div className="item">
            <div className="label">Loại giao dịch</div>
            <div className="input">
              <MyDropdown onChange={this.drdLoaiGDChange} list={data.ListLGD} placeholder="Chọn Loại giao dịch"/>
            </div>
          </div>
          <div className="item">
            <div className="label">BH/Susidy</div>
            <div className="input">
              <MyDropdown width="100%" list={data.ListNVBH} onChange={this.drdBHChange}/>
              <Button label="Bảo&nbsp;lãnh" style={{fontFamily:"Tahoma", marginLeft:10}} disabled={!this.state.bh} onClick={this.btnBaoLanhClick} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Box1;
