import React from 'react';
import {DataTable} from 'primereact/datatable';
import {ContextMenu} from 'primereact/contextmenu';
import {Column} from 'primereact/column';
  // var data = require('./dataTable.json');
  // var dt = data.table1.data;
  var dt;
class MyTable extends React.Component{
  constructor(props){
    super(props);
    this.state = { menu: [
                    {label: 'View', icon: 'pi pi-fw pi-search', command: (event) => this.viewItem(this.state.selectedItem)},
                    {label: 'Delete', icon: 'pi pi-fw pi-times', command: (event) => this.deleteItem(this.state.selectedItem)}
                  ],
                  dataTable: null,
    }
  }
  componentWillReceiveProps(nextProps){
    dt = this.props.data;
    this.setState({dataTable:dt});
    console.log(nextProps);
    if(this.props.data !== nextProps.data)
      {console.log(1);this.setState({dataTable: nextProps.data});
      }
  }
  viewItem = (item) => {
       alert(dt.indexOf(item));
   }
   deleteItem = (item)=> {
        dt.splice(dt.indexOf(item),1);
        this.setState({dataTable:dt});
    }
  onClick=(e)=>{
    console.log(dt);
    console.log(this.state.dataTable);
    alert(dt.indexOf(this.state.dataTable[e.key]));
  }
  render() {
       let columns = this.props.columns.map((col) => {
           return <Column key={col.key}  field={col.dataIndex} header={col.title} sortable="true" />;
       });

       return (
         <div>
           <ContextMenu model={this.state.menu} ref={el => this.cm = el} onHide={() => this.setState({selectedItem: null})}/>
           <DataTable value={this.state.dataTable}
           contextMenuSelection={this.state.selectedItem} onContextMenuSelectionChange={e => this.setState({selectedItem: e.value})}
           onContextMenu={e => this.cm.show(e.originalEvent)}
           onRowClick={this.onClick}
           style={{fontFamily:"Tahoma"}}>
             {columns}
           </DataTable>
         </div>

       );
   }
}
export default MyTable;
