import React from 'react';
// import {Button, Checkbox, Input} from 'antd';
import {Button} from 'primereact/button';
import {InputText} from 'primereact/inputtext';
import {InputTextarea} from 'primereact/inputtextarea';
import MyDropdown from './Dropdown';
import MyCheckbox from './Checkbox';
var data = require('./data.json');
// const { TextArea } = Input;
class Box3 extends React.Component {

  render() {
    return (
      <div className="box">
        <h1 style={{color:"blue"}} align="center">AQ/11P - 1 - 000001</h1>
        <h3 align="center">Thông tin khách hàng</h3>
        <div className="column" style={{width:"100%"}}>
          <div className="item">
            <div className="label">Tên KH</div>
            <div className="input">
              <MyDropdown list={data.ListNVBH}/>
            </div>
          </div>
          <div className="item">
            <div className="label">Số Đt</div>
            <div className="input">
              <MyDropdown width="100%" list={data.ListHTTT}/>
              {/*
              <Button style={{width:110, marginLeft:5}} type="primary">TT thuê bao</Button>
              <Button style={{width:110, marginLeft:5}} type="primary">Cước thuê bao</Button>
              */}
              <Button label="TT&nbsp;thuê&nbsp;bao" style={{marginLeft:5,fontFamily:"Tahoma"}} />
              <Button label="Cước&nbsp;thuê&nbsp;bao" style={{marginLeft:5,fontFamily:"Tahoma"}} />
            </div>
          </div>
          <div className="item">
            <div className="label">MST</div>
            <div className="input">
              <MyDropdown list={data.ListHTTT}/>
            </div>
          </div>
          <div className="item">
            <div className="label">Công ty</div>
            <div className="input">
              <MyDropdown list={data.ListHTTT}/>
            </div>
          </div>
          <div className="item">
            <div className="label">Địa chỉ</div>
            <div className="input">
              <MyDropdown list={data.ListHTTT}/>
            </div>
          </div>
          <div className="item">
            <div className="label">Tài khoản</div>
            <div className="input">
              <MyDropdown list={data.ListHTTT}/>
            </div>
          </div>
          <div className="item">
            <div className="label">ID</div>
            <div className="input">
              <MyDropdown list={data.ListHTTT}/>
            </div>
          </div>
          <div className="item">
            <div className="label">Ghi chú</div>
            <div className="input">
              <InputTextarea rows={5} style={{width:"100%"}} autoResize={true} />
            </div>
          </div>
          <div className="item">
            <div className="label">Thông tin BS</div>
            <div className="input">
              <MyDropdown list={data.ListHTTT}/>
            </div>
          </div>
          <hr color="teal" width="80%"/>
          <div className="item">
            <div className="label">Chưa thuế</div>
            <div className="input">
              {/* <Input disabled/>*/}
              <InputText keyfilter="money" style={{width:"100%"}}  disabled/>
            </div>
          </div>
          <div className="item">
            <div className="label">Thuế</div>
            <div className="input">
              {/* <Input disabled/>*/}
              <InputText keyfilter="money" style={{width:"100%"}}  disabled/>
            </div>
          </div>
          <div className="item">
            <div className="label">Chiết khấu</div>
            <div className="input">
              {/* <Input disabled/>*/}
              <InputText keyfilter="money" style={{width:"100%"}} disabled/>
            </div>
          </div>
          <div className="item">
            <div className="label">Tổng tiền</div>
            <div className="input">
              {/* <Input disabled/>*/}
              <InputText keyfilter="money" style={{width:"100%"}} disabled/>
            </div>
          </div>
          <MyCheckbox inputId="chk2" label="Bán hàng qua Fax, điện thoại."></MyCheckbox>
          <br/>
        </div>
      </div>
    );
  }
}
export default Box3;
