import React from 'react';
// import {Button, InputNumber, Checkbox} from 'antd';
import {Button} from 'primereact/button';
import {InputText} from 'primereact/inputtext';
import MyCheckbox from './Checkbox';
import MyDropdown from './Dropdown';
import MyTable from './Table';

var data = require('./data.json');
var dataTable = require('./dataTable.json');
// eslint-disable-next-line
Number.prototype.format = function () {
    return this.toString().split( /(?=(?:\d{3})+(?:\.|$))/g ).join( "," );
};
class Box2 extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      product: null,
      price: 0,
      discount: 0,
      quantity:1,
      bh:null,
      table1:[],
      table2:[]
    }
    this.baseState = this.state;
  }

  drdMatHangChange = item => {
    // console.log(this.state);
    this.drdGiaChange();

    document.getElementById("inputSL").value = 1;
    this.setState({product: item,
    quantity:1});
  }
  drdGiaChange = item => {
    let gia = item? item.value : null;
    this.setState({price: gia});
  }
  drdKhuyenMaiChange = item => {
    let km = item? item.value : 0;
    this.setState({discount: km});
  }
  txtSLBlur = e => {
    let sl = Number(e.target.value);
    sl = sl < 1 ? 1 : sl;
    sl = sl > 1000*1000 ? 1000*1000 : sl;
    document.getElementById("inputSL").value = sl;
    this.setState({quantity: sl});
  }
  btnThemClick =(event)=>{
    event.preventDefault();
    if(this.state.product)
      if(this.state.quantity)
        if(this.state.price){
          let arr = this.state.table1;
          let flag = true; // Cờ kiểm tra mặt hàng chưa có trong bảng
          let chietKhau = document.getElementById('chk1').checked && this.state.discount ? this.state.discount : 0;
          arr.map((item,i) =>{
            // Kiểm tra bản ghi trùng mã hàng, giá, chiết khấu.
            if(item.productID === this.state.product.productID &&
            item.price === this.state.price &&
            item.km === chietKhau){
              // Đã có -> tăng số lượng và cập nhật chiết khấu, tổng tiền.
              arr[i].quantity += this.state.quantity;
              let tongTien = arr[i].price * arr[i].quantity;
              let tienCK = tongTien*chietKhau/100;
              tongTien -= tienCK;
              arr[i].discount = `${tienCK.format()} (${chietKhau}%)`;
              arr[i].total = `${tongTien.format()} VNĐ`;
              flag = false;
            }
            return flag;
          });
          if(flag){ // Chưa có -> thêm mới 1 bản ghi.
            let tongTien = this.state.quantity * this.state.price;
            let tienCK = tongTien*chietKhau/100;
            tongTien -= tienCK;
            arr.push({productID: this.state.product.productID,
            quantity: this.state.quantity,
            price: this.state.price,
            vat:"10%",
            km: chietKhau,
            discount: `${tienCK.format()} (${chietKhau}%)`,
            total: `${tongTien.format()} VNĐ`});
            }
            console.log(this.state.table1);
          return  this.setState({table1:arr});
          }
    alert("Vui lòng nhập đầy đủ thông tin"); // Nhập thiếu thông tin.
  }
  render() {
    return (
      <form className="box" onSubmit={this.btnThemClick}>
        <div className="column" style={{width:"70%"}}>
          <div className="item">
            <div className="label">Mặt hàng</div>
            <div className="input">
              <MyDropdown onChange={this.drdMatHangChange} width="35%" list={data.ListMatHang} />
              <MyDropdown width="65%" list={data.ListMatHang} disabled={!this.state.product}/>
            </div>
          </div>
          <div className="item">
            <div className="label">Giá</div>
            <div className="input">
              <MyDropdown onChange={this.drdGiaChange} list={this.state.product?this.state.product.price : null} />
            </div>
          </div>
          <div className="item">
            <div className="label">Khuyến mại</div>
            <div className="input">
              <MyDropdown onChange={this.drdKhuyenMaiChange} value={this.state.discount} list={this.state.product?this.state.product.discount : null} disabled={!this.state.product}/>
            </div>
          </div>
        </div>
        <div className="column" style={{width:"30%", float:"right"}}>
          <div className="item">
            <div className="label">Số lượng</div>
            <div className="input">
              <InputText maxLength="8" id="inputSL" onBlur={this.txtSLBlur} keyfilter="pint" style={{minWidth:"50px"}}/>
            </div>
          </div>
          <Button label="Thêm" type="submit" onClick={this.btnThemClick} style={{width:"50%", height:50, minWidth:90, marginLeft:"27%", marginTop:10}} />
        </div>
        <MyCheckbox inputId="chk1" label="Tính chiết khấu khách hàng lẻ."></MyCheckbox>
        <br/>
        <MyTable data={this.state.table1} columns={dataTable.table1.columns}></MyTable>
        <br/>
        <MyTable data={this.state.table2} columns={dataTable.table2.columns}></MyTable>
      </form>
    );
  }
}
export default Box2;
