import React, {Component} from 'react';
import {Dropdown} from 'primereact/dropdown';

export class MyDropdown extends Component {
  constructor(props) {
    super(props);
    this.state = { value:null, listOptions:null};
  }
  componentWillReceiveProps(nextProps){
    if(this.props.list !== nextProps.list)
      {this.setState({value: null});
      this.props.onChange(null);}
  }
  handleChange = e => {
    const value = e.value;
    this.props.onChange(value);
    this.setState({value: value});
  }
  render() {
    return (
      <Dropdown
        style={{ width:this.props.width, height:'100%', minWidth:"50px", fontFamily:"Tahoma"}}
        value={this.state.value}
        placeholder={this.props.placeholder}
        filterPlaceholder= "Tìm kiếm"
        disabled={this.props.disabled}
        filter={true}
        showClear={true}
        optionLabel="value"
        options={this.props.list}
        onChange={this.handleChange}
    >
    </Dropdown>
    );
  }
}
MyDropdown.defaultProps = { width: "100%", placeholder: "Choose here.", list:[], disabled:false, onChange: name=>{console.log("default: " + name);} };
export default MyDropdown;
