import React, { Component } from 'react';
import {Calendar} from 'primereact/calendar';
var dateFormat = require('dateformat');
function validateDate(dateString)  //function to validate that the input string is a valid date formatted as "dd/mm/yyyy"
{
  dateString = dateString.replace(/ +/g, ""); // remove all white space.
  dateString = dateString.replace(/-/g,'/'); //replace all '-' with '/'.
  // First check for the pattern
  if(!/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(dateString))
      return false;
  // Parse the date parts to integers
  var parts = dateString.split("/");
  var day = parseInt(parts[0], 10);
  var month = parseInt(parts[1], 10);
  var year = parseInt(parts[2], 10);

  // Check the ranges of month and year
  if(year < 1000 || year > 3000 || month === 0 || month > 12)
      return new Date();

  var monthLength = [ 31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31 ];

  // Adjust for leap years
  if(year % 400 === 0 || (year % 100 !== 0 && year % 4 === 0))
      monthLength[1] = 29;
  // Check the range of the day
  return (day > 0 && day <= monthLength[month - 1]) ? [month,day,year].join("/") : new Date();
};
class MyCalendar extends Component {
  constructor(props){
    super(props);
    this.state = {date: null}
  }
  handleChange = e => {
      this.setState({date: e.value});
      // this.props.onChange(e.value);
  }
  handleSelect = e => {
    let date = dateFormat(e.value,"dd/mm/yyyy");
    console.log(date+"_sl");
    this.setState({date: date});
    this.props.onChange(date);
  }
  handleBlur = (e) =>{
    let date = dateFormat(validateDate(e.target.value),"dd/mm/yyyy"); //validating input date.
    this.props.onChange(date);
    this.setState({date: date});
  }
  render() {
    return (
      <Calendar style={{ width: "100%", height:"100%"}}
        value={this.state.date}
        placeholder="Ngày/tháng/năm"
        dateFormat="dd/mm/yy"
        maxDate={new Date()}
        onChange={this.handleChange}
        onSelect={this.handleSelect}
        showIcon={true}
        onBlur={this.handleBlur}
        monthNavigator={true} yearNavigator={true} yearRange="2000:2030"
        />
    );
  }
}
MyCalendar.defaultProps = {onChange: value=>{console.log(value);console.log(1);}}
export default MyCalendar;
